import React from 'react'
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';

import './styles.css';
import { Autoplay, Navigation, Pagination} from 'swiper/modules';

const SwiperImg = () => {
  const bg1 = {
    backgroundImage: `url(${process.env.PUBLIC_URL}/img/doterra1.jpg)`,
  }
  const bg2 = {
    backgroundImage: `url(${process.env.PUBLIC_URL}/img/doterra2.jpg)`,
  }
  const bg3 = {
    backgroundImage: `url(${process.env.PUBLIC_URL}/img/doterra3.jpg)`,
  }
  const bg4 = {
    backgroundImage: `url(${process.env.PUBLIC_URL}/img/doterra4.jpg)`,
  }
  return (
    <>
     <Swiper
        spaceBetween={30}
        centeredSlides={true}
        autoplay={{
          delay: 2500,
          disableOnInteraction: false,
        }}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        modules={[Autoplay, Pagination, Navigation]}
        className="mySwiper"
      >
        <SwiperSlide style={bg1}><p>  <br/>   </p></SwiperSlide>
        <SwiperSlide style={bg2}><p>  <br/>  </p></SwiperSlide>
        <SwiperSlide style={bg3}><p> <br/>  </p></SwiperSlide>
        <SwiperSlide style={bg4}><p>  <br/>   </p></SwiperSlide>
        
      </Swiper>
    </>
  )
}

export default SwiperImg
